import React, { useContext, useState, useEffect } from "react";
// import { CardDeck } from "react-bootstrap";
import ProjectCard from "./ProjectCard";
import { FirebaseContext } from "../config/firebase";
import { projectList } from "./projectlist";
const ProjectCards = (props) => {
  const context = useContext(FirebaseContext);
  const [projects, setProjects] = useState(null);

  useEffect(() => {
    async function projectListHook() {
      const response = await context.api.getProjects();
      if (projects == null) {
        setProjects(response);
      }
    }

    projectListHook();
  });
  function filterTopProjects() {
    return function (project) {
      if (props.OnlyTop) return project.TopProject === true;
      else return true;
    };
  }
  return (
    <div className="row project_cards_container">
      {projects != null
        ? projects.filter(filterTopProjects()).map((proj) => {
            return (
              <div key={proj.id} className="col-12 col-md-6 col-lg-4">
                <ProjectCard
                  key={proj.id}
                  ProjectImg={proj.ProjectImg}
                  ProjectTitle={proj.ProjectTitle}
                  ProjectDesc={proj.ProjectDesc}
                  ProjectURL={proj.ProjectURL}
                  ProjectGit={proj.ProjectGit}
                  ProjectDesigns={proj.ProjectDesigns}
                  ProjectTechs={Object.keys(proj.ProjectTechs).map(
                    (key) => proj.ProjectTechs[key]
                  )}
                />
              </div>
            );
          })
        : ""}
      {projects == null
        ? projectList.filter(filterTopProjects()).map((proj) => {
            return (
              <div key={proj.id} className="col-12 col-sm-6 col-md-4">
                {/* <ProjectCard
            key={proj.id}
            ProjectImg={proj.ProjectImg}
            ProjectTitle={proj.ProjectTitle}
            ProjectDesc={proj.ProjectDesc}
            ProjectURL={proj.ProjectURL}
            ProjectGit={proj.ProjectGit}
            ProjectDesigns={proj.ProjectDesigns}
            ProjectTechs={proj.ProjectTechs}
            /> */}
              </div>
            );
          })
        : ""}
    </div>
  );
};

export default ProjectCards;
